<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница"
      :class="{ ' pagination__link--disabled ': page === 1 }"
        @click="$event.preventDefault(); paginate(page - 1)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{ ' pagination__link--current ': pageNumber === page }"
        @click="$event.preventDefault(); paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница"
      :class="{ ' pagination__link--disabled ': page === pages }"
        @click="$event.preventDefault(); paginate(page + 1)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  // page - текущая страница, count - общее кол-во
  // товара, perPage - кол-во элементов на одной странице
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  data() {
    return {
      pageSwitch: this.page,
    }
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
  },
};
</script>
