<template>
    <main class="content container">
        <div class="content__top content__top--catalog">
            <h1 class="content__title">
                Каталог
            </h1>
            <span class="content__info">
                152 товара
            </span>
        </div>

        <div class="content__catalog">
            <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
                :category-id.sync="filterCategoryId" :color-id.sync="filteredColorId"></ProductFilter>

            <section class="catalog">
                <!--отрисовываем прелоудер-->
                <div v-if="productsLoading">Загрузка товаров...</div>
                <div v-if="productsLoadingFailed">Произошла ошибка при загрузки товаров!!
                    <button @click="loadProducts">Попробовать еще раз</button>
                </div>

                <!-- передаем параметр в ProductList, который называется products,
   и будем передавать сюда массив который храниться в сщстоянии компонента App  -->
                <ProductList :products="products" />
                <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />

            </section>

        </div>
    </main>
</template>

<script>
// import products from '@/data/products';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from "../config"

export default {
    components: { ProductList, BasePagination, ProductFilter },
    data() {
        return {
            filterPriceFrom: 0,
            filterPriceTo: 0,
            filterCategoryId: 0,
            filteredColorId: 0,


            page: 1,
            productsPerPage: 3,

            // состояние которе хранит ответ от сервера productsData
            productsData: null,

            //это свойство хранит статус загрузки данных с сервера
            productsLoading: false,

            //это свойство хранит статус правильности загрузки с сервера
            productsLoadingFailed: false,

        };
    },
    computed: {

        // возвращает отфильтрованный список товаров
        // filteredProducts() {
        //   // создаем переменную и помещаем ее products из products.js
        //   let filteredProducts = products;

        //   // логика фильтрации
        //   if(this.filterPriceFrom > 0) {
        //     filteredProducts = filteredProducts.filter(products => products.price > this.filterPriceFrom);
        //   }

        //   if(this.filterPriceTo > 0) {
        //     filteredProducts = filteredProducts.filter(products => products.price < this.filterPriceTo);
        //   }

        //   if(this.filterCategoryId > 0) {
        //     filteredProducts = filteredProducts.filter(products => products.categoryId === this.filterCategoryId);
        //   }

        //   if(this.filteredColorId !== '' ) {
        //     filteredProducts = filteredProducts.filter(colors => colors.colors.includes(this.filteredColorId) );
        //   }

        //   return filteredProducts;
        // },
        // вычисляемое свойство принимает не весь список товаров
        //  а только часть при помощи slice() массива products
        //продукты будут выбираться из отфильтрованного списка, поэтому применяетсяя filteredProducts
        products() {
            //выводим товары которые получили из сервера при помощи метода loadProducts
            //и который сохранили в productsData
            //для того чтоб список отображался сразу , еслти productsData есть то возвращаем  список товаров
            //а если нет, то пустой массив
            return this.productsData
                ? this.productsData.items.map(products => {
                    return {
                        ...products,
                        //чтоб картинка загружалась по url
                        image: products.image.file.url
                    }
                })
                : [];
        },
        // вычисляемое свойство возвращает общееколичество товаров
        countProducts() {
            //берем общее количество товаров из сервера pagination.total
            return this.productsData ? this.productsData.pagination.total : 0;
        },
    },
    methods: {
        // метод который загружает данные из сервера
        loadProducts() {
            //1 отправляем http запрос на сервер при помощи axios метод get
            //2 для паганинации задаем параметры page который раен page из состояния data
            //3 и limit который равен perpage - сколько товаров на странице отображать
            //4 перед загрузкой  свойство productsLoading будет true
            this.productsLoading = true;
            //перед загрузкой productsLoadingFailed ставим false
            this.productsLoadingFailed = false;
            clearTimeout(this.loadProductsTimer);
            this.loadProductsTimer = setTimeout(() => {
                axios.get(API_BASE_URL + `/api/products`, {
                    params: {
                        page: this.page,
                        limit: this.productsPerPage,
                        categoryId: this.filterCategoryId,
                        minPrice: this.filterPriceFrom,
                        maxPrice: this.filterPriceTo,
                        colorId: this.filteredColorId,
                    }
                })
                    //при успешном запросе ответ запишем в productsData
                    .then(response => this.productsData = response.data)
                    //при помощи catch ловим ошибки при загрузки с сервера
                    .catch(() => this.productsLoadingFailed = true)
                    //после загрузки productsLoading будет false
                    .then(() => this.productsLoading = false)
            }, 10)
        }
    },
    //добавляем наблюдатель чтоб при изменении страницы page при клике на паганации менялась страница
    watch: {
        //наблюдаем за page
        page() {
            //если изменилась page вызываем loadProducts
            this.loadProducts();
        },
        //отслеживание для фильтрации
        filterCategoryId() {
            this.loadProducts();
        },
        filterPriceFrom() {
            this.loadProducts();
        },
        filterPriceTo() {
            this.loadProducts();
        },
        filteredColorId() {
            this.loadProducts();
        }
    },
    //хук жизненного цикла created()
    //вызываем метод loadProducts в тот момент когда создается компонентн
    created() {
        this.loadProducts();
    }

};

</script>