<template>
    <div>
        <main class="content container">
            <div class="content__top">
                <ul class="breadcrumbs">
                    <li class="breadcrumbs__item">
                        <a class="breadcrumbs__link" href="index.html">
                            Каталог
                        </a>
                    </li>
                    <li class="breadcrumbs__item">
                        <a class="breadcrumbs__link">
                            Корзина
                        </a>
                    </li>
                </ul>

                <h1 class="content__title">
                    Корзина
                </h1>
                <span class="content__info">
                    {{ productsGet.length }} товара
                </span>
            </div>
            <div v-if="$store.state.cartLoading">Загрузка корзины...</div>
            <section v-else class="cart">
                <form class="cart__form form" action="#" method="POST">
                    <div class="cart__field">
                        <ul class="cart__list">
                            <CartItem v-for="item in productsGet" :key="item.productsId" :item="item" />
                        </ul>
                    </div>

                    <div class="cart__block">
                        <p class="cart__desc">
                            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
                        </p>
                        <p class="cart__price">
                            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
                        </p>

                        <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
                            Оформить заказ
                        </router-link>
                    </div>
                </form>
            </section>
        </main>
    </div>
</template>
    
    
    
<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from 'vuex';
import CartItem from "@/components/CartItem.vue";

export default {
    filters: { numberFormat },
    components: { CartItem },
    computed: {
        ...mapGetters({ productsGet: 'cartDetalsProducts', totalPrice: 'cartTotalPrice' }),

    }
};
</script>