import Vue from "vue";
import VueRouter from "vue-router";
import MainPage from "@/pages/MainPage";
import NotFoundedPage from "@/pages/NotFoundedPage";
import ProductPage from "@/pages/ProductPage";
import CartPage from "@/pages/CartPage";
import OrderPage from "@/pages/OrderPage";
import OrderInfoPage from "@/pages/OrderInfoPage"

Vue.use(VueRouter);

// массив где будут храниться пути
// path мы сами назначаем
const routes = [
    
    {name: 'main', component: MainPage, path: '/'},
    {name: 'product', component: ProductPage, path: '/product/:id'},
    {name: 'notFound', component: NotFoundedPage, path: '*'},
    {name: 'order', component: OrderPage, path: '/order'},
    {name: 'cartPage', component: CartPage, path: '/cart'},
    {name: 'orderInfo', component: OrderInfoPage, path: '/order/:id'},
    
];
//подключаем этот массив к экземпляру роутера
const router = new VueRouter({
    routes
});

export default router;