import BaseFormField from '@/components/BaseFormField';
export default {
    props: ['title', 'error', 'placeholder', 'value'],
    components: { BaseFormField },
    //так как не можем изменять props на прямую воспользуемся двунапрвленным вычисляемым свойством
    computed: {
        dataValue: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit('input', value);
            }
        }
    }
}