import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/config";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [],

        cartLoading: false,

        // для хранения ключа пользователя сосдаем отдельное свойство
        userAccessKey: null,
        //из апи получаем информацию о корзине/ нас интересует items[] в котором хранится список товаров вк корзине
        cartProductsData: [],

        orderInfo: []

    },
    mutations: {

        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo
            console.log((orderInfo.basket))
        },

        //мутация для сброса корзины при отправки заявки на сервер
        resertCart(state) {
            state.cartProducts = [];
            state.cartProductsData =[];
        },

        updateCartproductAmount(state, { productsId, amount }) {
            const item = state.cartProducts.find(item => item.productsId === productsId);

            if (item) {
                item.amount = amount;
            }
        },

        deleteCartProduct(state, productsId) {
            state.cartProducts = state.cartProducts.filter(item => item.productsId !== productsId)
        },


        incrementProductAmount(state, { productsId, amount }) {
            const item = state.cartProducts.find(item => item.productsId === productsId);

            if (item) {
                item.amount = amount + 1;
            }
        },

        dicrementProductAmount(state, { productsId, amount }) {
            const item = state.cartProducts.find(item => item.productsId === productsId);

            if (item) {
                item.amount = amount - 1;
            }
        },
        //мутация для изменения свойства userAccessKey
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey
        },
        //мутация дял изменения свойства cartProductData
        updateCartProductsData(state, items) {
            state.cartProductsData = items
        },
        //мутация для синхронизации товаров в корзине которые мы сами добавляем и тех товаров которые прилетают из api. формат данных товаров сами добавили и формат данных из  api разный
        //поэтому нужна эта мутация
        syncCartProducts(state) {
            //в свойство cartProducts мы будем помещать массив cartProductsData
            //через функцию map производим преобразование данных
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productsId: item.product.id,
                    amount: item.quantity
                }
            });
        }

    },
    getters: {
        cartDetalsProducts(state) {
            return state.cartProducts.map(item => {
                //чтобы информация о товаре добавленного в корзину прилетала из api
                //для этого сначала найдем этот товар в массиве cartProductsData 
                const product = state.cartProductsData.find(p => p.product.id === item.productsId).product;
                return {
                    ...item,
                    product: {
                        ...product,
                        image: product.image.file.url
                    }
                }
            });
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetalsProducts.reduce((acc, item) => item.product.price * item.amount + acc, 0);
        },


    },
    //в мутациях нельзя вызывать ассинхронные функции и для этого  есть actions которые не ограничены синхронностью функции
    //создаем actions для получения данных из api и полученные данные мы запишем в свойства usreAccessKey и cartProductsData
    actions: {
        loadOrderInfo(context, orderId) {
            return axios
            .get(API_BASE_URL + '/api/orders/' + orderId, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            })
            .then(response => {
                context.commit('updateOrderInfo', response.data);
            });
        },
        //первым аргументом в действии прилетает context. context содержить все теже методы что и vuex
        //метод comit вызываем чтоб сделать какую нибудь мутацию
        loadCart(context) {
            this.cartLoading = true;
            return axios
                .get(API_BASE_URL + '/api/baskets', {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                //при успешной загрузки будем вызывать мутации
                .then(responce => {
                    //сохраняем данные пользователя в localstorage
                    //для этого в localstorage будем записывать тоже самое что и лглобальное хранилище
                    //первый парамето - это то как мы назвываем то что будем хранить, а второй - это то что будет храниться в первом параметре
                    //и сперва будем проверять есть ли в свойстве userAccessKey в глобальном хранилище что нибудь
                    //и только если там ничего нет, то будем записывать данные и в локалсторедж и глобальное хранилище
                    if (!context.state.userAccessKey) {
                        localStorage.setItem('userAccessKey', responce.data.user.accessKey);
                        context.commit('updateUserAccessKey', responce.data.user.accessKey);
                    }
                    context.commit('updateCartProductsData', responce.data.items);
                    context.commit('syncCartProducts')

                    //действие loadCart будем вызывать в App
                })
                .then(() => this.productsLoading = false);
        },

        //это действие для добавления товаров в корзину из api, не мутация потому что мутация должна быть синхронной а не асинхронной
        addProductToCart(context, { productsId, amount }) {
            return new Promise((resolve) => setTimeout(resolve, 500)).then(() => {
                return axios
                    .post(
                        API_BASE_URL + '/api/baskets/products',
                        {
                            productId: productsId,
                            quantity: amount
                        },
                        {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            }
                        })
                    .then((responce) => {
                        context.commit('updateCartProductsData', responce.data.items);
                        context.commit('syncCartProducts');
                    });
            });
        },
        //действие по взаимодействию api и изменения количества товара в корзине
        updateCartProductAmount(context, { productsId, amount }) {
            context.commit('updateCartproductAmount', { productsId, amount });
            if (amount < 1) {
                return
            }

            return axios

                .put(
                    API_BASE_URL + '/api/baskets/products',
                    {
                        productId: productsId,
                        quantity: amount
                    },
                    {
                        params: {
                            userAccessKey: context.state.userAccessKey
                        }
                    })
                .then((responce) => {
                    context.commit('updateCartProductsData', responce.data.items);
                })
                .catch(() => {
                    context.commit('syncCartProducts');

                })
        },

        deleteProductFromCart(context, { productsId }) {
            context.commit('deleteCartProduct', { productsId })
            let areYouSure = confirm("Вы уверены, что хотите удалить товар?")
            if (!areYouSure) {
                return;
   
              }
            return axios
                .delete(
                    API_BASE_URL + '/api/baskets/products',
                    {
                        params: {
                            userAccessKey: context.state.userAccessKey
                        },
                        data: {
                            productId: productsId
                        },
                    })



                .then((responce) => {

                    context.commit('updateCartProductsData', responce.data.items);
                    context.commit('syncCartProducts');
                })
                .catch(() => {
                    console.log("Ой, что-то не так")
                });
        },
    }
});
