<template>
    <li class="cart__item product">
        <div class="product__pic">
            <img :src="item.product.image" width="120" height="120" alt="item.product.title">
        </div>
        <h3 class="product__title">
            {{ item.product.title }}
        </h3>
        <span class="product__code">
            Артикул:{{ item.product.id }}
        </span>
        <Counter :counter.sync="amountComp" />

        <b class="product__price">
            {{ item.product.price * item.amount | numberFormat }} ₽
        </b>

        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
            @click.prevent="deleteCartProduct(item.productsId)">
            <svg width="20" height="20" fill="currentColor">
                <use xlink:href="#icon-close"></use>
            </svg>
        </button>
    </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
// import { mapMutations } from 'vuex';
import Counter from './Counter.vue';
import { mapActions } from 'vuex';

export default {
    props: ['item'],
    components: { Counter },
    filters: { numberFormat },
    computed: {
        amountComp: {
            get() {
                return this.item.amount
            },
            set(value) {
                this.$store.dispatch('updateCartProductAmount', { productsId: this.item.productsId, amount: value })
            }
        },



    },

    methods: {
        // ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
        ...mapActions(['deleteProductFromCart']),

        deleteCartProduct() {
            this.deleteProductFromCart({ productsId: this.item.productsId })
        }
    }
}
</script>