<template>
    <label class="form__label">
        <!--   для возможности изменять input на другие виды форм ввода применяем slot              -->
        <slot></slot>
        <span class="form__value">{{ title}}</span>
        <span class="form__error" v-if="error">{{ error }}</span>
    </label>
</template>

<script>
    export default {
        props: ['title', 'error']
    }
</script>